@charset "utf-8";
/* Sub_style.css */
.txt_color1{
  color: blueviolet;
}
#bg_color1{
  background: #ccc;
}

/* 배경이미지 */
.bg_img{
  width: 342px;
  height: 228px;
  background: url('../images/banner2.png');
}
.num{
  color:#f00;
  font-weight: bold;
  font-size: 30px;
}
.btn{
  width: 40px;
  height: 30px;
  font-size: 20px;
  margin: 10px;
}
.btn2{
  width: 30px;
  line-height: 30px;
  background-color: #fff;
  color: #ccc;
  border:none;
  padding-bottom: 5px;
  font-size: 20px;
}
.btn2act{
  color: #333;
}
.btn3{
    width: 30px;
  line-height: 20px;
  background-color: #fff;
  border:none;
  font-size: 20px;
}
.num2{
  padding-top: 3px;
  font-size: 18px;
}
.price{
  font-size: 40px;
}
.count{
  display: flex;
  justify-content: space-between;
  width: 80px;
  padding: 2px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.pricebox{
padding-bottom: 20px;
border-bottom: 1px solid #ccc;
  display: flex;
  justify-content: space-between;
}
.result{
  display: flex;
  justify-content: flex-end;
}
.btns{
  display: flex;
  justify-content: flex-end;
}
.heart,.bell{
  width: 50px;
  height: 50px;
  margin-right: 20px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 3px;
}
.btn4{
  width: 420px;
  height: 50px;
  background-color: #5f0080;
  color: #fff;
  font-size: 28px;
  border-radius: 3px;
}